"use client";

import { useState, useEffect } from "react";
import { User, Bookmark, BookmarkCheck } from "lucide-react";
import Chatbot from "../chatbot/page";
import Profile from "./profile";

interface Scheme {
  id: number;
  title: string;
  description: string;
  publishDate: string;
  category: string;
  eligibility: string;
  documents: string[];
  applyLink: string;
}

const schemes: Scheme[] = [
  {
    id: 1,
    title: "Pradhan Mantri Awas Yojana-Urban 2.0",
    description:
      "Ambitious housing initiative by the Government of India, aimed at ensuring affordable housing for all in urban areas.",
    publishDate: "03/04/2025",
    category: "Housing",
    eligibility: "Low-income groups, EWS, and middle-income groups living in urban areas.",
    documents: ["Aadhaar Card", "Income Certificate", "Proof of Residence"],
    applyLink: "https://pmaymis.gov.in/",
  },
  {
    id: 2,
    title: "National Scholarship Portal",
    description:
      "Centralized portal for students to apply for scholarships provided by various government departments and ministries.",
    publishDate: "15/06/2024",
    category: "Education",
    eligibility: "Students from minority, SC/ST, OBC, and economically weaker backgrounds.",
    documents: ["Aadhaar Card", "Income Certificate", "Previous Marksheet", "Bank Account Details"],
    applyLink: "https://scholarships.gov.in/",
  },
  // ... add other schemes here
];

export default function CitizenDashboard() {
  const [activeTab, setActiveTab] = useState("schemes");
  const [bookmarks, setBookmarks] = useState<number[]>([]);
  const [compareList, setCompareList] = useState<number[]>([]);
  const [recommendedSchemes, setRecommendedSchemes] = useState<Scheme[]>([]);

  const loadRecommendations = () => {
    const saved = localStorage.getItem("recommendedSchemes");
    if (saved) setRecommendedSchemes(JSON.parse(saved));
  };

  useEffect(() => {
    loadRecommendations();
  }, []);

  const toggleBookmark = (id: number) => {
    setBookmarks((prev) =>
      prev.includes(id) ? prev.filter((bid) => bid !== id) : [...prev, id]
    );
  };

  const toggleCompare = (id: number) => {
    setCompareList((prev) =>
      prev.includes(id) ? prev.filter((cid) => cid !== id) : [...prev, id]
    );
  };

  return (
    <div className="p-6">
      {/* Tabs */}
      <div className="flex justify-between items-center border-b mb-6">
        <div className="flex space-x-4">
          {["schemes", "recommendations", "chatbot", "autofiller"].map((tab) => (
            <button key={tab} onClick={() => setActiveTab(tab)}
              className={`pb-2 px-4 font-medium ${activeTab === tab
                ? "border-b-4 border-indigo-600 text-indigo-600"
                : "text-gray-600 hover:text-indigo-500"}`}>
              {tab === "schemes" && "Schemes"}
              {tab === "recommendations" && "Recommendations"}
              {tab === "chatbot" && "Chatbot 🤖"}
              {tab === "autofiller" && "AutoFiller"}
            </button>
          ))}
        </div>
        <button onClick={() => setActiveTab("profile")}
          className={`p-2 rounded-full ${activeTab === "profile"
            ? "bg-indigo-100 text-indigo-600"
            : "text-gray-600 hover:text-indigo-500"}`}>
          <User className="w-6 h-6" />
        </button>
      </div>

      {/* Schemes Tab */}
      {activeTab === "schemes" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">All Schemes</h1>
          <div className="space-y-6">
            {schemes.map((scheme) => (
              <div key={scheme.id} className="border rounded-lg shadow-sm p-6 bg-white">
                <h2 className="text-xl font-bold mb-2">{scheme.title}</h2>
                <p className="text-gray-600">{scheme.description}</p>
                <p className="text-sm text-gray-500">Category: {scheme.category}</p>
                <button onClick={() => toggleBookmark(scheme.id)}>
                  {bookmarks.includes(scheme.id) ? <BookmarkCheck className="text-indigo-600 w-6 h-6" /> : <Bookmark className="text-gray-500 w-6 h-6" />}
                </button>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Recommendations Tab */}
      {activeTab === "recommendations" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">Recommended for You</h1>
          {recommendedSchemes.length === 0 ? (
            <p className="text-gray-600">Please fill your profile to get recommendations.</p>
          ) : (
            <div className="space-y-6">
              {recommendedSchemes.map((scheme) => (
                <div key={scheme.id} className="border rounded-lg shadow-sm p-6 bg-white">
                  <h2 className="text-xl font-bold mb-2">{scheme.title}</h2>
                  <p className="text-gray-600">{scheme.description}</p>
                  <p className="text-sm text-gray-500">Category: {scheme.category}</p>
                  <button onClick={() => toggleBookmark(scheme.id)}>
                    {bookmarks.includes(scheme.id) ? <BookmarkCheck className="text-indigo-600 w-6 h-6" /> : <Bookmark className="text-gray-500 w-6 h-6" />}
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Chatbot Tab */}
      {activeTab === "chatbot" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">Chatbot 🤖</h1>
          <Chatbot />
        </div>
      )}

      {/* AutoFiller Tab */}
      {activeTab === "autofiller" && (
        <div>
          <h1 className="text-3xl font-bold mb-6">AutoFiller</h1>
          <p className="text-gray-600">This module will pre-fill scheme forms using stored user data. (Demo coming soon 🚀)</p>
        </div>
      )}

      {/* Profile Tab */}
      {activeTab === "profile" && (
        <Profile onSave={loadRecommendations} allSchemes={schemes} />
      )}
    </div>
  );
}
